const _hoisted_12 = /*#__PURE__*/_createStaticVNode("<div class=\"main-col1\" data-v-e26706ca><div class=\"main-col1-text-box\" data-v-e26706ca><div class=\"main-col1-text\" data-v-e26706ca><h6 data-v-e26706ca>1. Üldiselt</h6><p data-v-e26706ca>Need reeglid baseeruvad ESL 5vs5 avatud liiga reeglistikule, kuid neid on muudetud vastavalt LANTech vajadustele. Järgmised reeglid on ausa mängu tagamiseks.</p><p data-v-e26706ca>Mänguvana (GM) võib igal hetkel muuta neid reegleid.</p><p data-v-e26706ca>Mängijate poolsed muudatused tuleb enne läbi rääkida GM ja tiimide vahel. Sellised kokkulepped tuleb ülesse kirjutada. Siia alla ei lähe muudatused, mida otsustab GM iseseisvalt, et tagada mängijate vahelist võrdsust.</p><h6 data-v-e26706ca>1.1 Nõustumised</h6><p data-v-e26706ca>GM võib muuta reegleid vastavalt oma soovile.</p><p data-v-e26706ca>Iga mängija peab olema tutvunud reeglitega enne võrgupeo algust.</p><p data-v-e26706ca>Võrgupeol osalejad nõustuvad nende reeglitega.</p><p data-v-e26706ca>Terve tiim vastutab oma liikmete eest - isegi, kui üks liikmetest rikub reegleid, siis karistada saavad kõik.</p><h6 data-v-e26706ca>1.2 Koodimine</h6><p data-v-e26706ca>Mängijad, kes jäävad koodimisega vahele diskvalitseeritakse võistlusest koos tiimiga.</p><p data-v-e26706ca>Koodimise alla kuuluvad aimbots, wallhacks, sound hacks, no recoil, no smoke, no flash, colored models ambient occlusion ja teised kolmanda osapoole programmid.</p><h6 data-v-e26706ca>2. Demod</h6><p data-v-e26706ca>Igal mängijal on kohustus salvestada enda vaatepunktist (POV) kogu mäng (konsooli “record [demoname]”). Juhul, kui GM või korraldusmeeskond küsib koodimise põhjustel seda salvestust ja seda pole, siis võib GM anda teisele tiimile võidu. GM võib kontrollida iga mängija demosid.</p><h6 data-v-e26706ca>2.1 Salvestuste päring</h6><p data-v-e26706ca>Mängija ei ole kohustatud saatma enda POV salvestust teisele mängijale. Koodimise kahtlusel peab sellest koheselt teavitama korraldusmeeskonda või GM-i. GM võib neid demosi jagada vajaduspõhiselt.</p><h6 data-v-e26706ca>2.3 Koodimise süüdistus</h6><p data-v-e26706ca>Süüdistused võetakse arvesse ainult võrgupeo ajal. Vajadusel kontrollib seda GM. Hilisemad süüdistused ei lähe arvesse.</p><h6 data-v-e26706ca>3. Seaded</h6><h6 data-v-e26706ca>3.1 Mängijate seaded</h6><h6 data-v-e26706ca>3.1.1 Keelatud scriptid</h6><p data-v-e26706ca>Kui sa ei ole kindel, kas mingi script on lubatud, siis see ilmselt ei ole. Siin on mõned näited.</p><ul data-v-e26706ca><li data-v-e26706ca>Lua scripts (näeb konsoolist)</li><li data-v-e26706ca>Stop shoot scripts (Use or AWP scripts)</li><li data-v-e26706ca>Center view scripts Turn scripts (180 or similar)</li><li data-v-e26706ca>No recoil scripts</li><li data-v-e26706ca>Burst fire scripts</li><li data-v-e26706ca>Rate changers (Lag scripts)</li><li data-v-e26706ca>FPS scripts</li><li data-v-e26706ca>Anti-flash scripts or binding (snd_* bindings)</li><li data-v-e26706ca>Bunnyhop scripts</li><li data-v-e26706ca>Stop sound scripts</li></ul><p data-v-e26706ca>Nende kasutamisel tuleb esimesel korral tähendab see mängu kaotust. Järgmisena turniirilt ban.</p><h6 data-v-e26706ca>3.1.2 Lubatud scriptid</h6><p data-v-e26706ca>Relvade ostu scriptid</p><p data-v-e26706ca>Salvestamise scriptid</p><h6 data-v-e26706ca>3.1.3 Mängu sisesed kuvad</h6><p data-v-e26706ca>Mängu sisesed kuvad on üldiselt keelatud v.a. järgmised:</p><ul data-v-e26706ca><li data-v-e26706ca>cl_showpos 1</li><li data-v-e26706ca>cl_showfps 1</li><li data-v-e26706ca>net_graph 0/1 (1 puhul ei tohi seda ära kasutada sihtimiseks)</li></ul><h6 data-v-e26706ca>3.1.4 Failide muutmine</h6><p data-v-e26706ca>HUD-i võib muuta, kui see ei muuda kuvatud informatsiooni hulka võrreldes originaaliga. Faile võib muuta ainult HUDi ja GUI.</p><p data-v-e26706ca>Ülejäänud failide muutmine on keelatud.</p><p data-v-e26706ca>Mõned näited:</p><ul data-v-e26706ca><li data-v-e26706ca>Muudetud mudelid</li><li data-v-e26706ca>Muudetud tekstuurid</li><li data-v-e26706ca>Muudetud helid</li></ul><h6 data-v-e26706ca>3.1.5 Tarkvara ja riistvara tööriistad</h6><p data-v-e26706ca>Muudatused graafika ja tekstuuride kohta on keelatud. Kuvad on samuti keelatud (Nvidia SLI Display, Rivatuner Overlays). Tööriistad, mis näitavad ainult FPS-i on lubatud.</p><p data-v-e26706ca>Ülalmainitud tööriistade kasutamine on keelatud</p><p data-v-e26706ca>Mängu jooksutamine 16-biti värvides on keelatud. Lisaks on keelatud muuta mathdrenabled ja mathdrlevel väärtusi.</p><h6 data-v-e26706ca>3.1.6 Config kausta sisu</h6><p data-v-e26706ca>Selles kaustas võib olla ainult config failid, kõik muu on keelatud.</p></div></div></div><div class=\"main-col2\" data-v-e26706ca><div class=\"main-col2-text-box\" data-v-e26706ca><div class=\"main-col2-text\" data-v-e26706ca><h6 data-v-e26706ca>4. Mängu reeglid</h6><h6 data-v-e26706ca>4.1 Mängunimi</h6><p data-v-e26706ca>Mängunimes ei tohi olla ebasündsust.</p><h6 data-v-e26706ca>4.2 Mängijad mängus</h6><p data-v-e26706ca>Kuna võistlus toimub 5vs5, siis mängu alustamiseks peavad kõik 10 mängijat kohal olema. Ainult mängijad, kes on kinnitatud enne võistluse algust võivad mängida.</p><h6 data-v-e26706ca>4.3 Võistluse ajal</h6><h6 data-v-e26706ca>4.3.1 Poole valik</h6><p data-v-e26706ca>Poole otsustamiseks tehakse serveri poolne automaatne noaround. Võitja tiim otsustab kummalt poolt nad otsustavad mängida kirjutades .stay või .switch.</p><h6 data-v-e26706ca>4.3.2 Serveri crash</h6><p data-v-e26706ca>Juhul, kui server jookseb kokku esimese 3 roundi juures, siis alustatakse mäng uuesti 0-0. </p><p data-v-e26706ca>Juhul, kui server jookseb kokku pärast seda, siis GM proovib viimase roundi taastada. Kui GM ei saa roundi taastatud, siis mängitakse edasi viimasest roundist ja võitja tiim alustab 2000 ja kaotaja tiim peab kirjutama konsooli kill, seda roundi ei võeta arvesse ja kaotaja algusrahaks tuleb 800.</p><h6 data-v-e26706ca>4.3.3 Mängija lahkumine</h6><p data-v-e26706ca>Juhul, kui mängija kaob mängust, siis tuleb kirjutada mängus .pause Mängijal on aega 15 minutit, et tagasi tulla. Pärast seda mängitakse edasi isegi, kui mängija ei jõua selle aja sees tagasi.</p><h6 data-v-e26706ca>4.3.4 Mängijate vahetus</h6><p data-v-e26706ca>Mängijaid ei tohi vahetada turniiri jooksul.</p><h6 data-v-e26706ca>4.3.5 Lisaaeg</h6><p data-v-e26706ca>Juhul, kui mäng jääb viiki, siis tuleb overtime.</p><h6 data-v-e26706ca>4.3.6 Mapide valimine</h6><p data-v-e26706ca>Parim ühest: Ban, ban, ban, ban, ban, ban ja valik</p><p data-v-e26706ca>Parim kolmest: Ban, ban, valik, valik, ban, ban ja valik</p><h6 data-v-e26706ca>4.3.7 Vead ja ja keelatud tegevused</h6><h6 data-v-e26706ca>4.3.7.1 Pomm</h6><p data-v-e26706ca>Mängijad ei tohi paigaldada pommi ligipääsmatutesse kohtadesse</p><h6 data-v-e26706ca>4.3.7.2 Boosting, Sky walking, sharking</h6><p data-v-e26706ca>Boostimine läbi seinte, lae või maa, kõndimine nähtamatutel äärtel, &quot;Skywalking&quot;, &quot;Map swimming&quot; ja &quot;Sharking&quot; on kõik keelatud tegevused</p><p data-v-e26706ca>Tavaline inimeste kuhjamine on lubatud</p><h6 data-v-e26706ca>4.3.7.3 Muu</h6><p data-v-e26706ca>Vigade kasutamine on keelatud. Uue vea leidmisel GM otsustab karistuse.</p><h6 data-v-e26706ca>4.4 Kaardid</h6><p data-v-e26706ca>Järgmised võimalikud kaardid:</p><ul data-v-e26706ca><li data-v-e26706ca>de_inferno</li><li data-v-e26706ca>de_nuke</li><li data-v-e26706ca>de_mirage</li><li data-v-e26706ca>de_dust2</li><li data-v-e26706ca>de_overpass</li><li data-v-e26706ca>de_vertigo</li><li data-v-e26706ca>de_ancient</li></ul><h6 data-v-e26706ca>4.5 Server</h6><p data-v-e26706ca>Kõik serverid toimuvad 128 tickistes serverites. Kõik serverid on lokaalses võrgus (LAN).</p><h6 data-v-e26706ca>5. Võistluse struktuur</h6><p data-v-e26706ca>Double elimination - Ülemine võistlus parim ühest, veerandfinaal parim kolmest, semi finaal parim kolmest, finaal parim viiest, alumine võistlus parim ühest </p><p data-v-e26706ca>Täpne võistluse struktuur ja tiimide väljapanek selgub kohapeal</p><br data-v-e26706ca><p data-v-e26706ca>ESL Gaming Network - baas reeglid</p></div></div></div><div class=\"main-timetable\" data-v-e26706ca><div class=\"main-timetable-text-box\" data-v-e26706ca><div class=\"main-timetable-title1\" data-v-e26706ca><div data-v-e26706ca><h6 data-v-e26706ca>Ajakava</h6></div></div><div class=\"main-timetable-text\" data-v-e26706ca><p data-v-e26706ca>Reede, 2. detsember</p><p data-v-e26706ca>18:00: Uste avamine</p><br data-v-e26706ca><p data-v-e26706ca>18:00-20:00: set up ja warm-up</p><br data-v-e26706ca><p data-v-e26706ca>19:30-21:30: Dirt Rally 2.0 turniir</p><br data-v-e26706ca><p data-v-e26706ca>20:00- ...: CSGO turniir</p></div><hr data-v-e26706ca><div class=\"main-timetable-text\" data-v-e26706ca><p data-v-e26706ca>Laupäev, 3. detsember</p><p data-v-e26706ca>... -03:00: CSGO turniir</p><br data-v-e26706ca><p data-v-e26706ca>03:00-04:00: vaba kava</p></div></div></div>", 3)

